#navbar{
    position: fixed;
    width: 100%;
    z-index: 1;
    background-color: #000;
    text-align: center;
    padding-top: 5px;
    padding-bottom: 5px;
    color: #fff;
}

nav{
    padding-top: 10px;
}

nav ul li{
    display: inline-block;
}

nav ul li:not(:first-child){
    margin-left: 30px;
}

nav ul li:last-child{
    margin-right: 30px;
}

nav ul li a{
    display: inline-block;
    outline: none;
    color: #fff!important;
    text-transform: uppercase;
    text-decoration: none!important;
    font-size: 14px;
    letter-spacing: 1.2px;
    font-weight: 600;
    transition: 1s;
}

nav ul li a:hover{
    color: red!important;
}

