#project{
    margin: auto;
    padding: 0;
    width: 100%;
    height: 100%;
    margin-top: 100px;
    margin-bottom: 220px;
    /* display: flex;
    flex-direction: row;
    flex-wrap: wrap; */
    background-color: rgb(241, 241, 241);
}

#project .box{
    text-align: center;
    position: relative;
    width: 100%;
    height: 100%;
    margin-bottom: 40px;
    margin-top: 40px;
}

#project .box .imgBox{
    position: relative;
}

#project .box .imgBox img{
    width: 150px;
    height: 150px;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    border-radius: 30px;
}

#project .box:hover .imgBox img{
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
}

#project .box .details{
    position: absolute;
    top: -20px;
    left: 10px;
    bottom: 50px;
    right: 10px;
    background: rgba(0, 0, 0, 0.8);
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    border-radius: 30px;
}

#project .box:hover .details{
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}

#project .box .details .content{
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    text-align: center;
    padding: 40px;
    top: 32%;
    color: #fff;
}

#project .box .details .content h2{
    margin: 0;
    padding: 0;
    font-size: 21px;
    color: #fff;
}

#project .box .details .content p{
    margin: 10px;
    padding: 0;
}

.emphasis{
    font-weight: bold;
}

