.screen{
    position : absolute;
    width : 100%;
    height : 100vh;
    z-index: 3;
    overflow: hidden;
    background-color: black;
}

.screen p{
    font-size: 250px;
    font-weight: 800;
}

.landingBackground{
    color : #424242;
    position: absolute;
    top: 42%;
    width: 100%;
    line-height: 100%;
}

.landingBtn{
    position: absolute;
    top : 48%;
    left : 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.screen .landingBtn{
    display : block;
    padding : 24px 48px;
    font-weight: 700;
    font-size: 12px;
    color : rgba(255, 255, 255, 0.863);
    border : 2px solid rgba(255, 255, 255, 0.863);
    text-transform: uppercase;
    outline : none;
    overflow: hidden;
    background : none;
    z-index: 0;
    transition: 0.8s ease-out;
}

.landingBtn:hover{
    color : #101010;
}

.landingBtn:before{
    content : "";
    position : absolute;
    background : rgba(255, 255, 255, 0.863);
    bottom :0;
    left: 0;
    right : 0;
    top : 100%;
    z-index: -1;
    transition : top 0.8s ease-out;
}

.landingBtn:hover:before{
    top : 0;
}

