        margin-left: 7px;
    }
}

@media screen and (max-width: 320px){
    .screen{
        background-color: #000;
    }
    
    .landingBackground{
        top: 45%;
        text-align: center;
    }

    .screen p{
        font-size: 60px;
        font-weight: 900;
    }
    
    .screen .landingBtn{
        top : 46%;
        left : 50%;
        padding : 2px 4px;
        font-size: 14px;
        letter-spacing: 6px;
    }

    .hong{
        margin-left: 7px;
    }
}

